import UrlParser from '../../routes/url-parser';
import restaurantSource from '../../data/restaurant-source';
import { createRestaurantDetail } from '../../../templates/template-creator';
import LikeButtonInitiator from '../../utils/like-button-presenter';

const Detail = {
  async render() {
    return `
    <div id="restaurant" class="restaurant"></div>
    <div id="likeButtonContainer"></div>
        `;
  },

  async afterRender() {
    const url = UrlParser.parseActiveUrlWithoutCombiner();
    const restaurant = await restaurantSource.detailRestaurant(url.id);
    const restaurantContainer = document.querySelector('#restaurant');
    restaurantContainer.innerHTML = createRestaurantDetail(restaurant);

    LikeButtonInitiator.init({
      likeButtonContainer: document.querySelector('#likeButtonContainer'),
      restaurant: {
        id: restaurant.id,
        pictureId: restaurant.pictureId,
        name: restaurant.name,
        city: restaurant.city,
        rating: restaurant.rating,
      },
    });

    const reviewButton = document.querySelector('#reviewButton');
    const reviewForm = document.querySelector('#reviewForm');

    reviewButton.addEventListener('click', () => {
      reviewForm.style.display = 'block';
    });

    reviewForm.addEventListener('submit', async (event) => {
      event.preventDefault();

      const reviewName = document.querySelector('#reviewName').value;
      const reviewContent = document.querySelector('#reviewContent').value;

      try {
        const date = new Date().toISOString().split('T')[0];
        const newReview = {
          name: reviewName,
          review: reviewContent,
          date,
        };

        await restaurantSource.addReview(url.id, newReview);

        const reviewList = document.querySelector('.customerHeading + ul');
        const newReviewItem = `
          <li style="list-style-type: none">
            <div class="review-item">
              <p>${newReview.name}</p>
              <p>${newReview.review}</p>
              <p>${newReview.date}</p>
            </div>
          </li>
        `;
        reviewList.innerHTML += newReviewItem;

        document.querySelector('#reviewName').value = '';
        document.querySelector('#reviewContent').value = '';

        reviewForm.style.display = 'none';
      } catch (error) {
        console.error('Error adding review:', error);
      }
    });
  },
};

export default Detail;
