import restaurantSource from '../../data/restaurant-source';
import { createRestaurantTemplate } from '../../../templates/template-creator';

const restoData = {
  async render() {
    return `
    <div id="hero">
    <picture>
    <source media="(max-width: 600px)" srcset="../../../images/hero-image_4-small.jpg">
    <img src="../../../images/hero-image_4-large.jpg" width="450" alt="" class="gbrHero"/>
    </picture>

    <div class="herotxt">
    <h2>Rest-aurant</h2>
    <p>Find the best places to eat</p>
    </div>

  </div>

  <div class="contentHeroFlex">
  <div class="contentHeroFlexText">
  <h2>"Embark on a Flavorful Journey with Our Restaurant Recommender App, Bringing You Unforgettable Culinary Adventures at Every Corner of the City."</h2><br>
  <p>Rest-aurant</p>
  </div>
  <picture>
  <source media="(max-width: 600px)" srcset="../../../images/hero-image_1-large.jpg" class="imgFlex">
  <img src="../../../images/hero-image_1-large.jpg" alt="" class="imgFlex">
  </picture>
  </div>

  <h2 class="expl">üìçExplore Restaurantüìç</h2>
  <div id="restaurant-list" class="grid-container"></div>
      `;
  },

  async afterRender() {
    const restaurants = await restaurantSource.allRestoData();
    const restaurantContainer = document.getElementById('restaurant-list');
    restaurants.forEach((restaurant) => {
      restaurantContainer.innerHTML += createRestaurantTemplate(restaurant);
    });
  },
};

export default restoData;
